<template>
  <div class="uc-text-input">
    <label class="uc-text-input__label input-label" v-if="label" :for="id">{{label}}</label>
    <input ref="field" class="uc-text-input__input" type="text" :id="id" :value="value" :disabled="readonly" :placeholder="placeholder" @input="emitValue">
  </div>
</template>

<script>
import getID from '../../js/id-generator'
export default {
  name: 'uc-text-input',
  data () {
    return {
      id: null
    }
  },
  props: {
    label: String,
    placeholder: String,
    value: String,
    readonly: Boolean
  },
  methods: {
    emitValue (event) {
      this.$emit('input', event.target.value)
    },
    focus () {
      this.$refs.field.focus()
    }
  },
  created () {
    this.id = getID()
  }
}
</script>

<style lang="stylus">
.uc-text-input__input:disabled
  cursor not-allowed
</style>
