<template>
  <div class="uc-rich-text">
    <label class="input-label" v-if="label">{{label}}</label>
    <div ref="editor"></div>
  </div>
</template>

<script>
import Quill from 'quill'
export default {
  name: 'uc-rich-text',
  data () {
    return {
      editor: null
    }
  },
  props: {
    value: {
      type: String,
      default: ''
    },
    theme: {
      type: String,
      default: 'snow'
    },
    readonly: Boolean,
    placeholder: {
      type: String,
      default: 'Enter text...'
    },
    label: String
  },
  mounted () {
    this.editor = new Quill(this.$refs.editor, {
      theme: this.theme,
      readOnly: this.readonly,
      placeholder: this.placeholder
    })
    this.editor.root.innerHTML = this.value
    this.editor.on('text-change', () => this.update())
  },
  methods: {
    update () {
      this.$emit('input', this.editor.getText() ? this.editor.root.innerHTML : '')
    }
  }
}
</script>

<style lang="stylus">
@import "../../stylus/_vendor/quill/snow.styl"
@import "../../stylus/_vendor/quill/bubble.styl"

</style>
