<template>
  <router-view/>
</template>

<script>
import Themes from '../shared/js/themes.js'
import { mapMutations } from 'vuex'
export default {
  name: 'root-app',
  async created () {
    const response = await this.axios.get('system/information')
    this.applyCMSTheme(response.data.cms)
    this.setSystemInfo(response.data)
  },
  methods: {
    ...mapMutations(['setSystemInfo']),
    applyCMSTheme (cmsName) {
      const doc = document.documentElement
      const cmsProps = Themes[cmsName]
      Object.keys(cmsProps).forEach(selector => {
        doc.style.setProperty(selector, cmsProps[selector])
      })
    }
  }
}
</script>

