<template>
  <div class="details-catalog">
    <section class="details__static">
      <uc-text-input :value="details.displayName" :label="`Display name (${language})`" :readonly="true"></uc-text-input>
      <uc-text-input :value="details.name" :label="`Internal name`" :readonly="true"></uc-text-input>
      <uc-checkbox :label="'Show on website'" :checked="details.displayOnWebsite" :readonly="true"></uc-checkbox>
      <uc-checkbox :label="'Show prices with VAT'" :checked="details.showPricesIncludingVAT" :readonly="true"></uc-checkbox>
      <uc-select :label="'Default price group'" :options="priceGroups" :selected="detailsDefaultPriceGroup.guid" :readonly="true"></uc-select>
      <uc-select :label="'Catalog definition'" :options="catalogDefinitions" :selected="detailsDefinition.guid" :readonly="true"></uc-select>
      <div>
        <label class="input-label">Allowed price groups</label>
        <div class="details-catalog__pricegroups">
          <div class="details-catalog__pricegroup-check" v-for="pgroup in priceGroupCheckboxes" :key="pgroup.value">
            <uc-checkbox :label="pgroup.text" :checked="pgroup.checked" :value="pgroup.value" :readonly="true"></uc-checkbox>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import UcTextInput from '../../../shared/components/forms/uc-text-input.vue'
import UcCheckbox from '../../../shared/components/forms/uc-checkbox.vue'
import UcSelect from '../../../shared/components/forms/uc-select.vue'
import { createNamespacedHelpers, mapGetters as globalGetters } from 'vuex'
const { mapGetters } = createNamespacedHelpers('details')
export default {
  name: 'catalog-details',
  props: {
    language: String
  },
  computed: {
    ...mapGetters(['details', 'priceGroups', 'catalogDefinitions']),
    ...globalGetters(['systemInfo']),
    priceGroupCheckboxes () {
      return this.priceGroups.map(x => {
        return {
          ...x,
          checked: !~this.details.allowedPriceGroups.findIndex(y => y.guid === x.guid)
        }
      })
    },
    detailsDefinition () {
      return this.details.definition || {}
    },
    detailsDefaultPriceGroup () {
      return this.details.defaultPriceGroup || {}
    }
  },
  components: {
    UcTextInput,
    UcCheckbox,
    UcSelect
  }
}
</script>

<style lang="stylus">
.details__static
  padding 35px 20px 20px 20px
  display grid
  grid-template-columns 1fr 1fr
  align-items center
  grid-row-gap 36px
.details-catalog__pricegroups
  margin-top 16px
.details-catalog__pricegroup-check
  margin-top 10px
</style>
