<template>
  <div class="uc-image" :style="{width, height}" :class="{cover, readonly}" @click="clickHandler">
    <img v-if="!fallback" :class="{cover}" :src="src" :alt="alt" @error="showDefault">
    <camera-icon v-if="fallback" />
  </div>
</template>

<script>
import CameraIcon from '../icons/camera'
export default {
  name: 'uc-image',
  data () {
    return {
      fallback: false
    }
  },
  props: {
    src: String,
    alt: String,
    cover: Boolean,
    width: { type: String, default: 'auto' },
    height: { type: String, default: 'auto' },
    readonly: Boolean
  },
  components: {
    CameraIcon
  },
  methods: {
    showDefault () {
      this.fallback = true
    },
    clickHandler () {
      this.$emit('click')
    }
  }
}
</script>

<style lang="stylus">
.uc-image
  cursor pointer
  box-shadow 2px 2px 7px rgba(0,0,0,.3)
  &.readonly
    cursor default
    pointer-events none
  img
    width 100%
    height auto
    display block
  svg
    box(45%)
    display block
    margin auto
    opacity .2
</style>
