<template>
  <div class="details-category">
    <section class="details-category__static">
      <div>
        <uc-image :src="details.mediaURL" :width="'250px'" :readonly="true"></uc-image>
      </div>
      <div class="details-category__static-right">
        <uc-text-input :value="details.displayName" :label="`Display name (${language})`" :readonly="true"></uc-text-input>
        <uc-text-input :value="details.name" :label="`Internal name`" :readonly="true"></uc-text-input>
        <uc-rich-text :value="details.description" :label="`Description (${language})`" :readonly="true"></uc-rich-text>
        <uc-checkbox :label="'Show on website'" :checked="details.displayOnWebsite" :readonly="true"></uc-checkbox>
        <uc-select :label="'Catalog definition'" :options="categoryDefinitions" :selected="detailsDefinition.guid" :readonly="true"></uc-select>
      </div>
    </section>
  </div>
</template>

<script>
import UcTextInput from '../../../shared/components/forms/uc-text-input.vue'
import UcRichText from '../../../shared/components/editors/uc-rich-text.vue'
import UcCheckbox from '../../../shared/components/forms/uc-checkbox.vue'
import UcSelect from '../../../shared/components/forms/uc-select.vue'
import UcImage from '../../../shared/components/uc-image.vue'
import { createNamespacedHelpers, mapGetters as globalGetters } from 'vuex'
const { mapGetters } = createNamespacedHelpers('details')
export default {
  name: 'category-details',
  props: {
    language: String
  },
  computed: {
    ...mapGetters(['details', 'priceGroups', 'categoryDefinitions']),
    ...globalGetters(['systemInfo']),
    detailsDefinition () {
      return this.details.definition || {}
    }
  },
  components: {
    UcTextInput,
    UcCheckbox,
    UcSelect,
    UcImage,
    UcRichText
  }
}
</script>

