<template>
  <section v-if="details" class="details-view">
    <header class="details-view__header">
      <div>
        <span class="details__type-label">{{detailsTypeSingular}}</span>
        <h2>{{details.name}}</h2>
      </div>
      <div class="details-view__header-right">
        <uc-select-simple :options="languages" :selected="displayedCulture" @selected="changeLanguage"></uc-select-simple>
        <info-widget>
          <label class="input-label">Created</label>
          <span>{{details.createdBy}}</span><br>
          <span>{{details.createdOn | format('DD. MMM YYYY', locale)}}</span>
          <label class="input-label">Modified</label>
          <span>{{details.modifiedBy}}</span><br>
          <span>{{details.modifiedOn | format('DD. MMM YYYY', locale)}}</span>
        </info-widget>
        <div class="details-view__delete" :class="{confirmToggled}">
          <span class="" @click="toggleConfirm">Delete</span>
          <transition name="popover-right">
            <div v-show="confirmToggled" class="dropdown-overlay details__delete-overlay">
              <button class="btn danger details__delete-btn" @click="deleteItem">Yes, delete this</button>
              <button class="btn" @click="toggleConfirm">Cancel</button>
            </div>
          </transition>
        </div>
      </div>
    </header>
    <component :is="displayedNode.nodeType" :language="displayedCulture"></component>
  </section>
</template>

<script>
import { nodeTypeApiMap, catalog, category } from '../../js/tree-node-types'
import CatalogDetails from './catalog-details.vue'
import CategoryDetails from './category-details.vue'
import InfoWidget from './info-widget.vue'
import UcSelectSimple from '../../../shared/components/forms/uc-select-simple.vue'
import { createNamespacedHelpers, mapGetters } from 'vuex'
const detailsStore = createNamespacedHelpers('details')
const builderStore = createNamespacedHelpers('builder')
export default {
  name: 'details-section',
  data () {
    return {
      loadDetailsLocked: false,
      bufferedDetailsGuid: null,
      currentDetailsGuid: null,
      typeNameMap: {
        productCategory: 'Category',
        productCatalog: 'Catalog'
      },
      selectedCulture: null,
      confirmToggled: false
    }
  },
  computed: {
    ...detailsStore.mapGetters(['details']),
    ...builderStore.mapGetters(['displayedNode', 'dragInfo']),
    ...mapGetters(['systemInfo', 'languages', 'locale']),
    detailsTypeSingular () {
      return this.typeNameMap[this.displayedNode.nodeType]
    },
    displayedCulture () {
      return this.selectedCulture ? this.selectedCulture : this.systemInfo.currentCultureCode
    }
  },
  watch: {
    displayedNode () {
      if (this.dragInfo.dragging) return
      if (!this.displayedNode.guid) {
        this.setDetails(undefined)
        return
      }
      this.bufferedDetailsGuid = this.displayedNode.guid
      this.getDetails()
    }
  },
  components: {
    productCatalog: CatalogDetails,
    productCategory: CategoryDetails,
    UcSelectSimple,
    InfoWidget
  },
  methods: {
    ...detailsStore.mapMutations(['setDetails', 'setCatalogDefinitions', 'setCategoryDefinitions', 'setPriceGroups']),
    getDetailsBase () {
      return this.axios.get(`${nodeTypeApiMap[this.displayedNode.nodeType]}/details/${this.displayedNode.guid}/${this.displayedCulture}`)
    },
    getPriceGroups () {
      if (this.displayedNode.nodeType === 'productCategory') {
        return new Promise((resolve) => {
          resolve({ data: [] })
        })
      }
      return this.axios.get(`catalogs/pricegroups/${this.displayedNode.guid}`)
    },
    async getDetails () {
      if (!this.loadDetailsLocked) {
        this.loadDetailsLocked = true
        this.currentDetailsGuid = this.displayedNode.guid
        const promises = [this.getDetailsBase(), this.getPriceGroups()]
        const [details, priceGroups] = await Promise.all(promises)
        this.populateDetails(details, priceGroups)
      }
    },
    populateDetails (details, priceGroups) {
      this.loadDetailsLocked = false
      if (this.currentDetailsGuid !== this.bufferedDetailsGuid) {
        this.getDetails()
        return
      }
      this.setDetails(details.data)
      this.setPriceGroups(priceGroups.data)
    },
    toggleConfirm (event) {
      event.preventDefault()
      this.confirmToggled = !this.confirmToggled
      if (this.confirmToggled) {
        window.setTimeout(() => {
          window.addEventListener('click', this.closeHandler)
        }, 10)
      }
    },
    deleteItem () {
      this.confirmToggled = false
      this.axios.delete(`${nodeTypeApiMap[this.displayedNode.nodeType]}/${this.displayedNode.guid}`)
        .then(() => {
          this.$emit('deleted')
        })
    },
    closeHandler () {
      window.removeEventListener('click', this.closeHandler)
      this.confirmToggled = false
    },
    changeLanguage (culture) {
      this.bufferedDetailsGuid = this.displayedNode.guid
      this.selectedCulture = culture
      this.getDetails()
    }
  },
  async created () {
    const catalogDefinitions = await this.axios.get(`${nodeTypeApiMap[catalog]}/definitions`)
    const categoryDefinitions = await this.axios.get(`${nodeTypeApiMap[category]}/definitions`)
    this.setCatalogDefinitions(catalogDefinitions.data)
    this.setCategoryDefinitions(categoryDefinitions.data)
  }
}
</script>

