  .details-view
    margin-bottom 60px
  .details-view__header
    border-bottom 1px solid $col_light_gray
    margin 0 20px
    display flex
    justify-content space-between
    align-items center
    h2
      font-weight normal
      margin 4px 50px 20px 0
      display inline-block
      max-width 310px
      truncate()
  .details__type-label
    display block
    font-weight 600
    font-size 0.5625rem
  .details-view__header-right
    display grid
    grid-template-columns auto auto auto
    grid-column-gap 20px
    align-items baseline
    justify-items end
  .details-view__delete
    position relative
    display inline-block
    font-size 0.8125rem
    color $col_red
    cursor pointer
    padding 4px 8px
    border-radius 4px
    transition background-color 200ms
    &.confirmToggled
      background-color lighten($col_red, 85%)
  .details__delete-overlay
    transform-origin top right
    display flex
    right 0
    left auto !important
    margin-top 3px
</style>
