import * as types from './mutation-types'

const mutations = {
  [types.SET_DETAILS] (state, details) {
    state.details = details
  },
  [types.SET_CATALOG_DEFINITIONS] (state, definitions) {
    state.catalogDefinitions = definitions
  },
  [types.SET_CATEGORY_DEFINITIONS] (state, definitions) {
    state.categoryDefinitions = definitions
  },
  [types.SET_PRICE_GROUPS] (state, priceGroups) {
    state.priceGroups = priceGroups
  }
}

export default mutations
